h1 {
    font-family: Lato, Helvetica, sans-serif;
    font-weight: 700;
    color: #000;
    margin: 6.4rem 0 3.2rem;
}

h1 {
    font-size: 3.2rem;
    line-height: 3.6rem;
}

@media only screen and (max-width: 768px) {
    h1 {
        font-size: 3rem;
        line-height: 3.4rem;
    }
}

.content {
    flex: 1;
    display: flex;
    margin-top: 1.6rem;
    margin-bottom: 3.2rem;
}

.content article header {
    margin-top: 6.4rem;
    margin-bottom: 3.2rem;
}

.content article header h1 {
    font-size: 4.2rem;
    line-height: 4.6rem;
    margin: 0;
}

@media only screen and (max-width: 768px) {
    .content article header h1 {
        font-size: 4rem;
        line-height: 4.4rem;
    }
}

html {
    line-height: 1.15;
    -webkit-text-size-adjust: 100%;
}

*, *:after, *:before {
    box-sizing: inherit;
}

.content article header h1 {
    font-size: 4.2rem;
    line-height: 4.6rem;
    margin: 0;
}

p {
    font-size: 17px;
    font-family: Merriweather,Georgia,serif;
}

article {
    margin: 0 auto;
    max-width: 90rem;
    width: 100%;
    padding-left: 2rem;
    padding-right: 2rem;
}

.calendar-emoji {
    font: normal normal normal 25px ForkAwesome;
    font-size: 20px;
}

.pin-emoji {
    font: normal normal normal 25px ForkAwesome;
    font-size: 20px;
    padding-left: 21px;
}

.display-inline {
    display: inline;
    padding-left: 7px;
}