.row {
    align-content: center;
    --bs-gutter-x: 1.5rem;
    --bs-gutter-y: 0;
    display: flex;
    flex-wrap: wrap;
    margin-top: calc(var(--bs-gutter-y) * -1);
    margin-right: calc(var(--bs-gutter-x) / -2);
    margin-left: calc(var(--bs-gutter-x) / -2);
}


.row > * {
    position: relative;
}

.cta-full .cta-full-img-box {
    padding: 0 10px 70px 0;
}

.pt-2 {
    padding-top: .5rem !important;
}

.mt-4 {
    margin-top: 1.5rem !important;
}

.col-6 {
    flex: 0 0 auto;
    width: 50%;
}

.text-center {
    text-align: center !important;
}

.card .card-body {
    padding: 1.5rem;
}

.p-0 {
    padding: 30 !important;
}

.card-body .skill-icon {
    width: 80px;
    height: 80px;
    line-height: 80px;
    -webkit-transition: all 0.5s ease;
    transition: all 0.5s ease;
}

.rounded-circle {
    border-radius: 50% !important;
}

.shadow-lg {
    box-shadow: 0 1rem 3rem rgba(0, 0, 0, .500) !important;
}

.d-inline-block {
    display: inline-block !important;
}

.card-body .skill-icon img {
    width: 50px;
}

img,
svg {
    vertical-align: middle;
}

.skill-badge {
    width: auto;
    background-color: rgba(0, 123, 254, 0.08);
    height: 18px;
    right: 17%;
    top: 5%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-shadow: 0 0 20px 0 rgb(243 246 247 / 5%);
    box-shadow: 0 0 20px 0 rgb(243 246 247 / 5%);
    color: #01a479 !important;
}

.container-center-option {
    margin: 0 auto;
    max-width: 90rem;
    width: 100%;
    padding-left: 2rem;
    padding-right: 2rem;
}

.badge {
    display: inline-block;
    padding: .35em .65em;
    font-size: 1.3em;
    font-weight: 700;
    line-height: 1;
    color: #fff;
    text-align: center;
    white-space: nowrap;
    vertical-align: baseline;
    border-radius: .25rem;
}

.titles h4 {
    font-size: 4.2rem;
    line-height: 4.6rem;
    margin: 0;
    text-align: center;
    padding-top: 50px;
}

.pFrist{
    padding-top: 10px;
}

.container-title p {
    font-size: 17px;
    font-family: Merriweather,Georgia,serif;
    margin: 0;
    text-align: center;
}

.block-center span {
    position: center;
}

.body2 article header h1 {
    font-size: 4.2rem;
    line-height: 4.6rem;
    margin: 0;
    text-align: center;
    padding-top: 50px;
}