.footer {
    text-align: center;
    font-size: 150%;
    position: relative;
    bottom: 0;
    padding-bottom: 5px;
    width: 100%;
    margin: auto;
}

.centered .options ul {
    list-style: none;
    margin: 3rem 0 1rem;
    padding: 0;
}

.centered .options ul li {
    display: block;
    position: relative;
}

.centered .options ul li a {
    color: #424242;
}

.centered .options {
    text-align: center;
}

.centered .options h1 {
    font-size: 3.0rem;
    line-height: 4.6rem;
    margin: 0;
}

.centered .socialMedia {
    text-align: center;
}

.centered .socialMedia h1 {
    margin-top: 2rem;
    margin-bottom: 0.5rem;
}

.centered .socialMedia h2 {
    margin-top: 1rem;
    margin-bottom: 0.5rem;
    font-size: 1.8rem;
}

@media only screen and (max-width: 768px) {
    .centered .socialMedia h2 {
        font-size: 2rem;
    }
}

.centered .socialMedia ul {
    list-style: none;
    margin: 2rem 0 0;
    padding: 0;
}

.centered .socialMedia ul li {
    display: inline-block;
    position: relative;
}

.centered .socialMedia ul li a {
    color: #212121;
    text-transform: uppercase;
    margin-left: 1rem;
    margin-right: 1rem;
    font-size: 1.6rem;
}

.centered .socialMedia ul li a:hover,
.centered .socialMedia ul li a:focus {
    color: #1565c0;
}

@media only screen and (max-width: 768px) {
    .centered .socialMedia ul li a {
        font-size: 1.4rem;
    }
}

.centered .socialMedia ul li a i {
    font-size: 3.2rem;
}

@media {
    body.colorscheme-auto .content .list ul li .title {
        color: #dadada;
    }

    body.colorscheme-auto .content .list ul li .title:hover,
    body.colorscheme-auto .content .list ul li .title:focus {
        color: #42a5f5;
    }

    body.colorscheme-auto .content .centered .socialMedia ul li a {
        color: #dadada;
    }

    body.colorscheme-auto .content .centered .socialMedia ul li a:hover,
    body.colorscheme-auto .content .centered .socialMedia ul li a:focus {
        color: #42a5f5;
    }
}