.containerFiverr {
    margin: 0 auto;
    max-width: 90rem;
    width: 100%;
    padding-left: 2rem;
    padding-right: 2rem;
}

h1 {
    font-size: 3.2rem;
    line-height: 3.6rem;
}

h1 {
    font-family: Lato, Helvetica, sans-serif;
    font-weight: 700;
    color: #000;
}

.date{
    font-family: Merriweather,Georgia,serif;
}

.list ul {
    margin: 3.2rem 0;
    list-style: none;
    padding: 0;
}

.list ul:not(.pagination) li {
    display: flex;
}

.list ul li {
    font-size: 17px;
}

.list ul li .date {
    display: inline-block;
    flex: 1;
    width: 20rem;
    text-align: right;
    margin-right: 3rem;
}

.content {
    flex: 1;
    display: flex;
    padding-top: 0.5rem;
    margin-bottom: 3.2rem;
}

.list ul li .title {
    font-size: 17px;
    flex: 2;
    color: #212121;
    font-family: Lato, Helvetica, sans-serif;
    font-weight: 700;
}