@media {
    body.colorscheme-auto {
        color: #dadada;
        background-color: #212121;
    }

    body.colorscheme-auto a {
        color: #42a5f5;
    }

    body.colorscheme-auto h1,
    body.colorscheme-auto h2,
    body.colorscheme-auto h3,
    body.colorscheme-auto h4,
    body.colorscheme-auto h5,
    body.colorscheme-auto h6 {
        color: #dadada;
    }

    body.colorscheme-auto code {
        background-color: #424242;
        color: #dadada;
    }

    body.colorscheme-auto pre code {
        background-color: inherit;
        color: inherit;
    }

    body.colorscheme-auto blockquote {
        border-left: 2px solid #424242;
    }

    body.colorscheme-auto table td,
    body.colorscheme-auto table th {
        border: 2px solid #dadada;
    }
}

@media {
    body.colorscheme-auto .content .list ul li .title {
        color: #dadada;
    }

    body.colorscheme-auto .content .list ul li .title:hover,
    body.colorscheme-auto .content .list ul li .title:focus {
        color: #42a5f5;
    }

    body.colorscheme-auto .content .centered .about ul li a {
        color: #dadada;
    }

    body.colorscheme-auto .content .centered .about ul li a:hover,
    body.colorscheme-auto .content .centered .about ul li a:focus {
        color: #42a5f5;
    }
}

@media {
    body.colorscheme-auto .navigation a,
    body.colorscheme-auto .navigation span {
        color: #dadada;
    }

    body.colorscheme-auto .navigation a:hover,
    body.colorscheme-auto .navigation a:focus {
        color: #42a5f5;
    }
}

@media only screen and (max-width: 768px) {
    body.colorscheme-auto .navigation .navigation-list {
        background-color: #212121;
        border-top: solid 2px #424242;
        border-bottom: solid 2px #424242;
    }
}

@media only screen and (max-width: 768px) {
    body.colorscheme-auto .navigation .navigation-list .menu-separator {
        border-top: 2px solid #dadada;
    }
}

@media only screen and (max-width: 768px) {
    body.colorscheme-auto .navigation #menu-toggle:checked + label > i {
        color: #424242;
    }
}

@media {
    body.colorscheme-auto .navigation i {
        color: #dadada;
    }

    body.colorscheme-auto .navigation i:hover,
    body.colorscheme-auto .navigation i:focus {
        color: #42a5f5;
    }

    body.colorscheme-auto .navigation .menu-button i:hover,
    body.colorscheme-auto .navigation .menu-button i:focus {
        color: #dadada;
    }
}

@media {
    body.colorscheme-auto .float-container a {
        color: #dadada;
        background-color: #424242;
    }

    body.colorscheme-auto .float-container a:hover,
    body.colorscheme-auto .float-container a:focus {
        color: #42a5f5;
    }
}

@media only screen and (max-width: 768px) {
    body.colorscheme-auto .float-container a:hover,
    body.colorscheme-auto .float-container a:focus {
        color: #dadada;
    }
}

.wrapper {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    width: 100%;
}

b,
strong {
    font-weight: 700;
}

.content {
    flex: 1;
    display: flex;
    margin-top: 1.6rem;
    margin-bottom: 3.2rem;
}

.centered {
    display: flex;
    align-items: center;
    justify-content: center;
}

.centered .about {
    text-align: center;
}

.centered .about h1 {
    margin-top: 2rem;
    margin-bottom: 0.5rem;
}

.centered .about h2 {
    margin-top: 1rem;
    margin-bottom: 0.5rem;
    font-size: 1.8rem;
}

@media only screen and (max-width: 768px) {
    .centered .about h2 {
        font-size: 2rem;
    }
}

.centered .about ul {
    list-style: none;
    margin: 3rem 0 1rem;
    padding: 0;
}

.centered .about ul li {
    display: inline-block;
    position: relative;
}

.centered .about ul li a {
    color: #212121;
    text-transform: uppercase;
    margin-left: 1rem;
    margin-right: 1rem;
    font-size: 1.6rem;
}

.centered .about ul li a:hover,
.centered .about ul li a:focus {
    color: #1565c0;
}

@media only screen and (max-width: 768px) {
    .centered .about ul li a {
        font-size: 1.4rem;
    }
}

.centered .about ul li a i {
    font-size: 3.2rem;
}

.img-me {
    vertical-align: middle;
    border-radius: 10%;
    border: 5px solid #dee2e6;
}

.about {
    padding-bottom: 3%;
}