button {
    display: inline-block;
    border-radius: 3px;
    background-color: #dadada;
    border: none;
    color: #424242;
    text-align: center;
    font-size: 24px;
    padding: 20px;
    width: 200px;
    transition: all 0.5s;
    cursor: pointer;
    margin: 7px;
}

button span {
    cursor: pointer;
    display: inline-block;
    position: relative;
    transition: 0.5s;
}

button:hover {
    transform: scale(1.15);
}

.options {
    padding-top: 2%;
}

@media only screen and (max-width: 768px) {
    .options {
        padding-top: 7%;
    }
}