html {
    box-sizing: border-box;
    font-size: 67%;
}

a {
    font-weight: 300;
    color: #1565c0;
    text-decoration: none;
    transition: all 0.25s ease-in;
}

.container {
    margin: 0 auto;
    max-width: 96%;
    width: 100%;
    padding-left: 2rem;
    padding-right: 2rem;
}

.navigation {
    height: 6rem;
    width: 100%;
}

.navigation a,
.navigation span {
    display: inline;
    font-size: 1.6rem;
    font-family: Lato, Helvetica, sans-serif;
    font-weight: 700;
    line-height: 6rem;
    color: #212121;
}

.navigation a:hover,
.navigation a:focus {
    color: #1565c0;
}

.navigation .navigation-title {
    letter-spacing: 0.1rem;
    text-transform: uppercase;
}

.navigation .navigation-list {
    float: right;
    list-style: none;
    margin-bottom: 0;
    margin-top: 0;
}

@media only screen and (max-width: 768px) {
    .navigation .navigation-list {
        position: absolute;
        top: 6rem;
        right: 0;
        z-index: 5;
        visibility: hidden;
        opacity: 0;
        padding: 0;
        max-height: 0;
        width: 100%;
        background-color: #fafafa;
        border-top: solid 2px #e0e0e0;
        border-bottom: solid 2px #e0e0e0;
        transition: opacity 0.25s, max-height 0.15s linear;
    }
}

.navigation .navigation-list .navigation-item {
    float: left;
    margin: 0;
    position: relative;
}

@media only screen and (max-width: 768px) {
    .navigation .navigation-list .navigation-item {
        float: none !important;
        text-align: center;
    }

    .navigation .navigation-list .navigation-item a,
    .navigation .navigation-list .navigation-item span {
        line-height: 5rem;
    }
}

.navigation .navigation-list .navigation-item a,
.navigation .navigation-list .navigation-item span {
    margin-left: 1rem;
    margin-right: 1rem;
}

@media only screen and (max-width: 768px) {
    .navigation .navigation-list .separator {
        display: none;
    }
}

@media only screen and (max-width: 768px) {
    .navigation .navigation-list .menu-separator {
        border-top: 2px solid #212121;
        margin: 0 8rem;
    }

    .navigation .navigation-list .menu-separator span {
        display: none;
    }
}

.navigation #dark-mode-toggle {
    margin: 1.7rem 0;
    font-size: 2.4rem;
    line-height: inherit;
    bottom: 2rem;
    left: 2rem;
    z-index: 100;
    position: fixed;
}

.navigation #menu-toggle {
    display: none;
}

@media only screen and (max-width: 768px) {
    .navigation #menu-toggle:checked + label > i {
        color: #e0e0e0;
    }

    .navigation #menu-toggle:checked + label + ul {
        visibility: visible;
        opacity: 1;
        max-height: 100rem;
    }
}

.navigation .menu-button {
    display: none;
}

@media only screen and (max-width: 768px) {
    .navigation .menu-button {
        display: inline;
        float: right;
        margin: 1.5rem 0;
        font-size: 2.4rem;
        font-weight: 400;
    }
}

.navigation .menu-button i:hover,
.navigation .menu-button i:focus {
    color: #000;
}

.navigation i {
    color: #212121;
    cursor: pointer;
}

.navigation i:hover,
.navigation i:focus {
    color: #1565c0;
}

.subnavigation {
    display: none;
}

.navigation-item :hover .subnavigation {
    display: block;
}